import React, { useState } from "react";
import styles from "./Modal.module.scss";  // Estilos espec√≠ficos do modal

interface ModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSave: (newPrompt: string) => void;
  currentPrompt: string;
}

const Modal: React.FC<ModalProps> = ({ isOpen, onClose, onSave, currentPrompt }) => {
  const [newPrompt, setNewPrompt] = useState(currentPrompt);

  const handleSave = () => {
    onSave(newPrompt); // Envia o novo prompt para o componente pai
    onClose(); // Fecha o modal
  };

  return isOpen ? (
    <div className={styles.modalOverlay}>
      <div className={styles.modalContent}>
        <h2>Alterar Prompt</h2>
        <textarea
          className={styles.textarea}
          value={newPrompt}
          onChange={(e) => setNewPrompt(e.target.value)}
        />
        <div className={styles.modalButtons}>
          <button onClick={onClose} className={styles.cancelButton}>
            Cancelar
          </button>
          <button onClick={handleSave} className={styles.saveButton}>
            Salvar
          </button>
        </div>
      </div>
    </div>
  ) : null;
};

export default Modal;
